import React, { Component } from 'react';
import axios from "axios";
import url from "./../../UrlConfig";
import { withRouter } from 'react-router';
import './Edit.css';


class Edit extends Component {

  constructor(props) {
    super(props);
    this.state = { 
        currentTitle: this.props.match.params.title,
        currentYear: this.props.match.params.year,
        title: "",
        year: 2000,
        active: false, 
        error: ""
        }
  }

  handleTitle = (event) => this.setState({ title: event.target.value});

  handleYear = (event) =>  { 
    if(event.target.value < 2000) {
    } else { 
      this.setState({ year: event.target.value});
    }
  }

  handleActive = (event) =>{
    console.log("value: ", event.target.checked)
    this.setState({ active: event.target.checked})
  } 

  doSubmit = async () => {
    const data ={
      title: this.state.title,
      year: this.state.year,
      active: this.state.active
    }
    try {
      await axios.put(`${url}/api/row/${this.state.currentTitle}/${this.state.currentYear}`, data)
      this.props.history.push('/specialisto_eiles');
    } catch (e) {
      console.log("klaida: ", e)
      if(e.response.status === 400){
           this.setState({
          error: "Kurti aktyvios eilės negalima, nes kita eilė jau yra aktyvuota.",
        });
        console.log("klaida: ", e.response)
      }
      
    }
  }

  handleSubmit = (event) => {
    this.doSubmit();
    event.preventDefault();
  }

  render() {
    return (
         <form onSubmit={this.handleSubmit}>
            <div className ="form-row align-items-center">
              <div className="col-sm-3 my-1">
                <label className="sr-only" for="inlineFormInputName">Pavadinimas ({this.state.title}):</label>
                <input type="text" className="form-control" id="inlineFormInputName" value={this.state.title}
                    onChange={this.handleTitle} placeholder={this.state.title}/>
                  
              </div>

              <div className="col-sm-3 my-1">
                <label className="sr-only" for="inlineFormInputName">  Metai ({this.state.year}):</label>
                <input type="number" min={2000} max={3000} 
                    value={this.state.year}
                    onChange={this.handleYear} className="form-control" id="inlineFormInputName" placeholder="Eilės metai"/>
              </div>
              
              <div className="col-auto my-1">
                <div className="form-check">
                  <input className="form-check-input" type="checkbox" checked={this.state.active}
                    onChange={this.handleActive} id="autoSizingCheck2"/>
                  <label className="form-check-label" for="autoSizingCheck2">
                  {this.state.active ? "Aktyvi" : "Neaktyvi"} eilė
                  </label>
                </div>
              </div>

              <div className="col-auto my-1">
                <button type="submit" className="btn btn-primary">Išsaugoti</button>
              </div>


                <h3>{this.props.pavadinimas}</h3>
            </div>
         {this.state.error != "" && (
              <div className="col-auto">
                <div className="alert alert-primary" role="alert">
                  {this.state.error }
                </div>
              </div>
            )}
        </form>
    );
  }

  componentDidMount = () => {
    axios.get(`${url}/api/row/${this.state.currentTitle}/${this.state.currentYear}`)
      .then((atsakymas) => {
        this.setState({
          title: atsakymas.data.title, 
          year: atsakymas.data.year,
          active: atsakymas.data.active
        })
      })
      .catch((klaida) => {
        console.log("Klaida skaitant eiles: ", klaida)
      })
    }
}
 export default withRouter(Edit);