import React, { Component } from "react";
import PropTypes from "prop-types";
import axios from "axios";
import DeleteModal from "../DeleteModal/DeleteModal"
import Edit from "../Edit/Edit"

class SpecEile extends Component {
  render() {
    return (
      <tbody>
        <tr>
          <td>{this.props.pavadinimas}</td>
          <td  type="number">{this.props.metai}</td>
          <td>{this.props.aktyvus ? "Aktyvi" : "Neaktyvi"}</td>
          <td>
            <button type="button" className="btn btn-primary" onClick={this.props.handleEdit} >
            Redaguoti
            </button>
          </td>
          <td>
            <button type="button" className="btn btn-primary" data-toggle="modal"
              data-target={"#modal-" + this.props.id + "-" + this.props.metai} >
            Trinti
            </button>
            <DeleteModal
              handleDelete={this.props.handleDelete}
              id={"modal-" + this.props.id + "-" + this.props.metai}>
            Ar tikrai norite ištrinti eilę {this.props.pavadinimas} {this.props.metai}?
            </DeleteModal>
          </td>
        </tr>
      </tbody>
    );
  }
}

SpecEile.propTypes = {
  pavadinimas: PropTypes.string.isRequired,
  metai: PropTypes.number.isRequired,
  aktyvus: PropTypes.bool.isRequired,
  handleDelete: PropTypes.func.isRequired,
  handleEdit: PropTypes.func.isRequired,
  id: PropTypes.string.isRequired

};

export default SpecEile;