import React, { Component } from 'react';
import axios from 'axios';
import url from './../../UrlConfig';
import PropTypes from "prop-types";
import './SpecEiles.css';
import SpecEile from '../SpecEile/SpecEile.js';
import { withRouter } from 'react-router';
import DeleteModal from "../DeleteModal/DeleteModal"



class SpecEiles extends Component {
    constructor(props){
        super(props);
        this.state = {
            eiles: []

        }
    }



    render(){
        const unikalus = "labai"
        if (this.state.eiles === []) {
            return <div>Duomenys dar kraunami... Prašome palaukti...</div>
        }
        var eiles = this.state.eiles.map((eile, index) => {
            return (  
                <SpecEile
                    id={"" + index}
                    key={eile.title + eile.year} 
                    pavadinimas={eile.title}
                    metai={eile.year}
                    aktyvus={eile.active}
                    handleDelete={(e) => this.handleDelete(e, eile.title, eile.year, this.props.kazkas)}
                    handleEdit={(e) => this.handleEdit(e, eile.title, eile.year)}
                    />
               
            );
        });
        return (<table className="table table-hover">
                    <thead>
                     <tr>
                      <th scope="col">Pavadinimas</th>
                      <th scope="col">Metai</th>
                      <th scope="col">Aktyvi/Neaktyvi eilė</th>
                      <th scope="col">&nbsp;</th>
                      <th scope="col">&nbsp;</th>
                     </tr>
                    </thead>
                        {eiles}
                </table>);
    }

    componentDidMount = () => {
        axios.get(`${url}/api/row`)
          .then((atsakymas) => {
              this.setState({eiles: atsakymas.data})
            })
          .catch((klaida) => {
              console.log("Klaida skaitant eiles: ", klaida)
            })
    }



    
  handleDelete = (e, title, year) => {
     console.log("it works with remove!");
	axios.delete(`${url}/api/row/${title}/${year}`)
	.then(res => {
                axios.get(`${url}/api/row`)
          .then((atsakymas) => {
              this.setState({eiles: atsakymas.data})

              
            })
          .catch((klaida) => {
              console.log("Klaida skaitant eiles: ", klaida)
            })
    console.log(res.data)
  })
  .catch(err => {
    console.log(err)
  })
  e.preventDefault()
}


handleEdit = (e, title, year) => 
 this.props.history.push(`/edit/${title}/${year}`);
    
}

export default withRouter(SpecEiles);